import React, { useContext } from "react";
import { Link, useNavigate } from "react-router-dom";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import { UserContext } from "./UserContext";
import {
  Container,
  Row,
  Col,
  Card,
  Form as BootstrapForm,
  Button,
} from "react-bootstrap";
import "./RegisterForm.css";

const RegisterForm = () => {
  const { users, setCurrentUser } = useContext(UserContext);
  const navigate = useNavigate();

  const validationSchema = Yup.object().shape({
    email: Yup.string()
      .email("Nieprawidowy adres e-mail")
      .required("Pole jest wymagane"),
    password: Yup.string()
      .min(6, "Haso musi mie co najmniej 6 znakw")
      .matches(/[!@#$%^&*(),.?":{}|<>]/, "Haso musi zawiera co najmniej jeden znak specjalny")
      .required("Pole jest wymagane"),
    confirmPassword: Yup.string()
      .oneOf([Yup.ref("password"), null], "Hasa musz by identyczne")
      .required("Pole jest wymagane"),
    imieINazwisko: Yup.string().required("Pole jest wymagane"),
    miasto: Yup.string().required("Pole jest wymagane"),
    kategoria: Yup.string().required("Pole jest wymagane"),
  });

  const initialValues = {
    email: "",
    password: "",
    confirmPassword: "",
    imieINazwisko: "",
    miasto: "",
    kategoria: "",
  };

  const handleRegister = (values, { setSubmitting, setFieldError }) => {
    
    const userExists = users.some((u) => u.email === values.email);
    if (userExists) {
      setFieldError(
        "email",
        "Uytkownik o podanym adresie e-mail ju istnieje"
      );
      setSubmitting(false);
      return;
    }

    
    const newUser = {
      id: users.length + 1, 
      email: values.email,
      haslo: values.password,
      imieINazwisko: values.imieINazwisko,
      miasto: values.miasto,
      kategoria: values.kategoria,
    };

    setCurrentUser(newUser);
    navigate("/ustawienia-konta");
  };

  return (
    <Container fluid className="pageBackground">
      <Row className="mt-5">
        <Col md={{ span: 6, offset: 3 }}>
          <Card>
            <Card.Body>
              <Card.Title>Formularz <span style={{ color: "#f8b296" }}>rejestracyjny</span></Card.Title>
              <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={handleRegister}
              >
                {({ isSubmitting }) => (
                  <Form>
                    <BootstrapForm.Group className="formGroup">
                      <BootstrapForm.Label>Imi i Nazwisko</BootstrapForm.Label>
                      <Field
                        type="text"
                        name="imieINazwisko"
                        as={BootstrapForm.Control}
                        placeholder="Imi i Nazwisko"
                      />
                      <ErrorMessage
                        name="imieINazwisko"
                        component="div"
                        className="alert alert-danger"
                      />
                    </BootstrapForm.Group>
                    <BootstrapForm.Group className="formGroup">
                      <BootstrapForm.Label>E-mail</BootstrapForm.Label>
                      <Field
                        type="email"
                        name="email"
                        id="email"
                        as={BootstrapForm.Control}
                        placeholder="E-mail"
                      />
                      <ErrorMessage
                        name="email"
                        component="div"
                        className="alert alert-danger"
                      />
                    </BootstrapForm.Group>
                    <BootstrapForm.Group className="formGroup">
                      <BootstrapForm.Label>Haso</BootstrapForm.Label>
                      <Field
                        type="password"
                        name="password"
                        id="password"
                        as={BootstrapForm.Control}
                        placeholder="Haso"
                      />
                      <ErrorMessage
                        name="password"
                        component="div"
                        className="alert alert-danger"
                      />
                    </BootstrapForm.Group>
                    <BootstrapForm.Group className="formGroup">
                      <BootstrapForm.Label>Potwierd haso</BootstrapForm.Label>
                      <Field
                        type="password"
                        name="confirmPassword"
                        id="confirmPassword"
                        as={BootstrapForm.Control}
                        placeholder="Potwierd haso"
                      />
                      <ErrorMessage
                        name="confirmPassword"
                        component="div"
                        className="alert alert-danger"
                      />
                    </BootstrapForm.Group>
                    <BootstrapForm.Group className="formGroup">
                      <BootstrapForm.Label>Miasto</BootstrapForm.Label>
                      <Field as="select" name="miasto" className="formLoginControl">
                        <option value="">Wybierz miasto</option>
                        <option value="Warszawa">Warszawa</option>
                        <option value="Krakw">Krakw</option>
                        <option value="Gdask">Gdask</option>
                        <option value="Wrocaw">Wrocaw</option>
                        <option value="Pozna">Pozna</option>
                        <option value="Rzeszw">Rzeszw</option>
                      </Field>
                      <ErrorMessage
                        name="miasto"
                        component="div"
                        className="alert alert-danger"
                      />
                    </BootstrapForm.Group>
                    <BootstrapForm.Group className="formGroup">
                      <BootstrapForm.Label>Kategoria</BootstrapForm.Label>
                      <Field
                        as="select"
                        name="kategoria"
                        className="form-control"
                      >
                        <option value="">Wybierz kategori</option>
                        <option value="Wokal">Wokal</option>
                        <option value="Gitary">Gitary</option>
                        <option value="Instrumenty klawiszowe">
                          Instrumenty klawiszowe
                        </option>
                        <option value="Instrumenty perkusyjne">
                          Instrumenty perkusyjne
                        </option>
                        <option value="Instrumenty dte">
                          Instrumenty dte
                        </option>
                        <option value="Instrumenty smyczkowe">
                          Instrumenty smyczkowe
                        </option>
                        <option value="DJ">DJ</option>
                      </Field>
                      <ErrorMessage
                        name="kategoria"
                        component="div"
                        className="alert alert-danger"
                      />
                    </BootstrapForm.Group>
                    <Button
                      type="submit"
                      variant="primary"
                      className="w-100"
                      disabled={isSubmitting}
                    >
                      {isSubmitting ? "Przetwarzanie..." : "Zarejestruj si"}
                    </Button>
                  </Form>
                )}
              </Formik>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default RegisterForm;
